import React, { useState, useEffect } from "react";
import "antd/dist/antd.css";
import { Button, Modal } from "antd";
import { putUser } from "../../Axios/user";
import { Input, Checkbox, message } from "antd";

const EditUser = (props) => {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [user, setUser] = useState({});
    const [password, setPassword] = useState("");

    useEffect(() => {
        const data = {};
        data._id = props.record._id;
        data.firstName = props.record.firstName;
        data.lastName = props.record.lastName;
        data.email = props.record.email;
        data.username = props.record.username;
        data._createTime = props.record._createTime;
        data.userType = props.record.userType;

        setUser(data);
    }, []);

    const success = () => {
        message.success("");
    };

    const error = () => {
        message.error("");
    };

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        if (props.record.userType === "admin") {
            message.success("");
        } else {
            if (password.trim() !== "") {
                user.password;
            }
            const data = [];
            props.users.map((item) => {
                if (item._id === user._id) {
                    data.push(user);
                } else {
                    data.push(item);
                }
            });

            if (props.record.username === user.username) {
                props.setUsers(data);
                putUser(user);
                success();
            } else {
                var sum = 0;
                props.users.map((item) => {
                    if (item.username === user.username) {
                        sum++;
                    }
                });
                if (sum > 0) {
                    message.error("");
                    user.username = props.record.username;
                } else {
                    props.setUsers(data);
                    putUser(user);
                    success();
                }
            }
        }
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        if (props.record.userType === "admin") {
            message.success("");
        } else {
            const data = {};
            data._id = user._id;
            data.firstName = props.record.firstName;
            data.lastName = props.record.lastName;
            data.email = props.record.email;
            data.username = props.record.username;
            setUser(data);
            error();
        }
        setIsModalVisible(false);
    };

    const Inputstyle = {
        marginBottom: "10px",
        width: "70%",
    };

    const ModalAdmonRender = () => {
        return (
            <>
                <p>{user.username}</p>
                <p>{user.firstName}</p>
                <p>{user.lastName}</p>
                <p>{user.email}</p>
            </>
        );
    };

    return (
        <>
            {props.record.userType === "admin" ? (
                <Button type="primary" onClick={showModal}>
                    
                </Button>
            ) : (
                <Button type="primary" onClick={showModal}>
                    
                </Button>
            )}
            <Modal
                title="/"
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                {props.record.userType === "admin" ? (
                    <ModalAdmonRender />
                ) : (
                    <>
                        <p>
                            
                            <Input
                                placeholder=""
                                value={user.username}
                                onChange={(e) => {
                                    setUser({
                                        ...user,
                                        username: e.target.value,
                                    });
                                }}
                                style={Inputstyle}
                            />
                        </p>
                        <p>
                            
                            <Input
                                placeholder=""
                                value={user.firstName}
                                onChange={(e) => {
                                    setUser({
                                        ...user,
                                        firstName: e.target.value,
                                    });
                                }}
                                style={Inputstyle}
                            />
                        </p>
                        <p>
                            
                            <Input
                                placeholder=""
                                value={user.lastName}
                                onChange={(e) => {
                                    setUser({
                                        ...user,
                                        lastName: e.target.value,
                                    });
                                }}
                                style={Inputstyle}
                            />
                        </p>
                        <p>
                            
                            <Input
                                placeholder=""
                                value={user.email}
                                onChange={(e) => {
                                    setUser({ ...user, email: e.target.value });
                                }}
                                style={Inputstyle}
                            />
                        </p>

                        <p>
                            
                            <Input
                                placeholder=""
                                value={password}
                                onChange={(e) => {
                                    setPassword(e.target.value);
                                }}
                                style={Inputstyle}
                            />
                        </p>
                    </>
                )}
            </Modal>
        </>
    );
};

export default EditUser;
